/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  transition: background-color 0.3s linear;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.to-right {
  text-align: right;
}

.boton-verde {
  display: inline-block;
  padding: 1rem 3rem;
  color: #FFFFFF;
  background-color: #71B100;
  font-weight: bold;
  text-align: center;
  margin: 2rem 0 0 0;
  border: none;
  cursor: pointer;
}
.boton-verde:hover {
  background-color: #619800;
}

.boton-verde-block {
  display: block;
  padding: 1rem 3rem;
  color: #FFFFFF;
  background-color: #71B100;
  font-weight: bold;
  text-align: center;
  margin: 2rem 0 0 0;
  border: none;
  cursor: pointer;
}
.boton-verde-block:hover {
  background-color: #619800;
}

.boton-amarillo {
  display: inline-block;
  padding: 1rem 3rem;
  color: #FFFFFF;
  background-color: #E08709;
  font-weight: bold;
  text-align: center;
  margin: 2rem 0 0 0;
  border: none;
  cursor: pointer;
}
.boton-amarillo:hover {
  background-color: #c77808;
}

.boton-amarillo-block {
  display: block;
  padding: 1rem 3rem;
  color: #FFFFFF;
  background-color: #E08709;
  font-weight: bold;
  text-align: center;
  margin: 2rem 0 0 0;
  border: none;
  cursor: pointer;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
}

.boton-rojo {
  display: inline-block;
  padding: 1rem 3rem;
  color: #FFFFFF;
  background-color: red;
  font-weight: bold;
  text-align: center;
  margin: 2rem 0 0 0;
  border: none;
  cursor: pointer;
}
.boton-rojo:hover {
  background-color: #e60000;
}

.boton-rojo-block {
  display: block;
  padding: 1rem 3rem;
  color: #FFFFFF;
  background-color: red;
  font-weight: bold;
  text-align: center;
  margin: 2rem 0 0 0;
  border: none;
  cursor: pointer;
  width: 100%;
  padding: 1.5rem 0;
}
.boton-rojo-block:hover {
  background-color: #e60000;
}

.darkmode {
  background-color: #1a1a1a;
}
.darkmode h1, .darkmode h2, .darkmode h3, .darkmode h4, .darkmode p {
  color: #e1e1e1;
}
.darkmode .property-content {
  background-color: #333333;
}
.darkmode .form legend {
  color: #e1e1e1;
}
.darkmode .form fieldset {
  border: 1px solid #e1e1e1;
}
.darkmode .form label {
  color: #e1e1e1;
}
.darkmode .form textarea, .darkmode .form select, .darkmode .form input:not([type=submit]) {
  background-color: #333333;
  color: #e1e1e1;
}
.darkmode .table-admin td {
  color: #e1e1e1;
}

.header {
  background-color: #333333;
}

.webp .inicio {
  background-image: url("../img/header.webp");
}

.no-webp .inicio {
  background-image: url("../img/header.jgp");
}

.inicio {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .inicio .content-header {
    min-height: 65rem;
  }
}

.content-header {
  padding: 3rem 0 2rem 0;
}
.content-header h1 {
  text-align: left;
  color: #FFFFFF;
  max-width: 60rem;
  font-weight: bold;
}
@media (min-width: 768px) {
  .content-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

.darkmode-btn {
  width: 5rem;
  margin: 1.5rem auto;
  filter: invert(10);
}
.darkmode-btn:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .darkmode-btn {
    width: 3rem;
    margin: 0 0 1rem auto;
  }
}

.barras-btn {
  width: 5rem;
  margin: 1.5rem auto;
}
@media (min-width: 768px) {
  .barras-btn {
    width: 3rem;
    margin: 0 0 1rem auto;
  }
}
@media (min-width: 768px) {
  .barras-btn {
    display: none;
  }
}

@media (min-width: 768px) {
  .bar {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
}

@media (min-width: 768px) {
  .navegator {
    display: flex;
    gap: 1.4rem;
  }
}

.navegator {
  opacity: 0;
  height: 0;
}
.navegator a {
  color: #FFFFFF;
  display: block;
  text-align: center;
  font-size: 1.8rem;
}
.navegator a:hover {
  color: #71B100;
}
@media (min-width: 768px) {
  .navegator {
    opacity: 1;
    height: auto;
  }
}

.visible {
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .icons-us {
    display: flex;
    gap: 2rem;
  }
}

.icon {
  text-align: center;
}
.icon img {
  height: 10rem;
}

@media (min-width: 768px) {
  .properties {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.property-content {
  background-color: #e1e1e1;
  padding: 2rem;
}
.property-content h3, .property-content p {
  margin: 0;
}
.property-content .precio {
  color: #71B100;
  font-weight: bold;
  font-size: 3rem;
}

.property-char {
  padding: 0;
  display: flex;
  max-width: 40rem;
}
.property-char li {
  display: flex;
  align-items: flex-end;
  gap: 1.5rem;
  flex: 1;
}
.property-char p {
  margin: 0;
  font-weight: bold;
}
.property-char img {
  width: 5rem;
}

.webp .contact {
  background-image: url("../img/encuentra.webp");
}

.no-webp .contact {
  background-image: url("../img/encuentra.jpg");
}

.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10rem 0;
}
.contact h2, .contact p {
  color: #FFFFFF;
}
.contact h2 {
  font-weight: bold;
  font-size: 4rem;
  margin: 2.5rem 0;
}
.contact p {
  margin: 1.8rem;
}

@media (min-width: 768px) {
  .double-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}

.article {
  margin-bottom: 2rem;
}
.article img {
  border-radius: 2rem;
}
.article h4, .article p {
  margin: 0;
}
.article .date span {
  color: #E08709;
}
@media (min-width: 768px) {
  .article {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}

.article-content h4::after {
  content: "";
  display: block;
  background-color: #71B100;
  height: 0.3rem;
  width: 50%;
}

.testigo {
  background-color: #71B100;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testigo blockquote {
  font-size: 2.4rem;
  padding-left: 5rem;
  position: relative;
}
.testigo blockquote::before {
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url("../img/comilla.svg");
  position: absolute;
  left: -2rem;
}
.testigo p {
  text-align: right;
  color: #FFFFFF;
}

.footer {
  background-color: #333333;
  padding: 1rem 0;
}
.footer p {
  margin: 0;
  color: #FFFFFF;
  padding: 1rem;
  text-align: center;
}
.footer .navegator {
  opacity: 1;
  height: auto;
}

.us h3 {
  margin: 0;
}
.us picture {
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .us {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
}

.form legend {
  font-size: 2rem;
  color: #333333;
}
.form fieldset {
  border: 1px solid #333333;
}
.form label {
  text-transform: uppercase;
  font-size: 1.6rem;
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}
.form textarea, .form select, .form input:not([type=submit]) {
  width: 100%;
  border-radius: 1.5rem;
  padding: 1rem;
  border: 1px solid #e1e1e1;
  margin-bottom: 1.5rem;
}
.form .opt-contact {
  display: flex;
  max-width: 30rem;
}
.form p {
  font-size: 1.6rem;
}

.image-update {
  width: 40rem;
}

.table-admin {
  width: 100%;
}
.table-admin th {
  background-color: #71B100;
  color: #FFFFFF;
}
.table-admin th, .table-admin td {
  text-align: center;
}
.table-admin td {
  padding: 2rem;
}
.table-admin img {
  width: 10rem;
  margin: 0 auto;
}

.alert {
  text-align: center;
  color: #e1e1e1;
  margin: 1rem 0;
  font-weight: bold;
  text-transform: uppercase;
}

.error {
  background-color: rgba(241, 11, 11, 0.836);
}